import React from "react"
import { Link } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"

import Footer from "../../components/footer"
import Avatar from "../../components/avatar"

import camPic from "../../images/cam.jpg"

function calculate_age(dob) {
  var diff_ms = Date.now() - dob.getTime()
  var age_dt = new Date(diff_ms)

  return Math.abs(age_dt.getUTCFullYear() - 1970)
}

const About = () => (
  <Layout>
    <SEO title="about" />

    <div className="mainWrapper">
      <Avatar src={camPic} />
      <div className="aboutWrapper">
        <h2>
          Well, I’m {calculate_age(new Date("7/17/1996"))} years old, and
          constantly learning new things. I grew up in a small town in
          Wisconsin, USA, and moved to Milwaukee when I was 18 to study Digital
          Marketing and Information Technology at The University of Wisconsin
          Milwaukee.
        </h2>
        <h2>
          When I was in University, I realised how important computers were
          becoming in the world, and found it interesting learning how
          everything worked behind the screen. So, I started teaching myself
          anything and everything, and found it so satisfying to see my work
          have a part in contributing to the web.
        </h2>
        <h2>
          Throughout studying, I was always seeing how I could apply the things
          I was learning into real-life projects that would benefit others and
          the way they interacted with the internet.
        </h2>
        <h2>
          The day after I graduated from University, I was on a plane to
          Amsterdam. I had a bag of clothes, a short-term place to stay, and an
          empty mind that was willing to learn.
        </h2>
        <h2>
          While I was in Amsterdam, I was still able to freelance, while
          continuing learning about modern Full Stack Web Development in more
          practical settings at a school called Codaisseur.
        </h2>
        <h2>
          This eventually led me to where I am now, where I get to work with and
          advise companies around the globe, learning about how product teams
          work, but also helping them understand some of the fundamentals I’ve
          learned with Framer.
        </h2>
      </div>
      <h3>
        <Link to="/" className="link">
          Back
        </Link>
      </h3>
    </div>
    <Footer />
  </Layout>
)

export default About
